body {
  font-family: monospace;
  font-size: 12px;
  color: white;
  background-color: black;
  margin: 1rem 0.5rem 0 1rem;
  text-align: left; }

.mancontent {
  max-width: 39rem;
  padding-right: 1rem; }

.manheader {
  margin: 0.5rem 0;
  height: 1rem;
  position: relative;
  max-width: 40rem; }
  .manheader span {
    position: absolute; }
  .manheader span:nth-child(1) {
    left: 0; }
  .manheader span:nth-child(2) {
    left: 40%; }
  .manheader span:nth-child(3) {
    right: 0; }

h1 {
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  margin: 15px 0 0 0; }

p {
  margin: 0 0 0.6rem 0; }

.subtext {
  margin-left: 3rem;
  max-width: 36rem; }
  @media (max-width: 576px) {
    .subtext {
      margin-left: 1rem;
      max-width: 36rem; } }

.doublesubtext {
  margin-bottom: 0.5rem;
  margin-left: 4.5rem;
  max-width: 33rem; }
  @media (max-width: 576px) {
    .doublesubtext {
      margin-left: 2rem;
      max-width: 34rem; } }

.triplesubtext {
  margin-bottom: 0.4rem;
  margin-left: 6rem;
  max-width: 30rem; }
  @media (max-width: 576px) {
    .triplesubtext {
      margin-left: 3rem;
      max-width: 32rem; } }

.bottomcut {
  margin-bottom: 0; }

a, a.active, a.visited, a.hover {
