  right: 1rem;
  padding: 0.5rem;
  text-align: right;
  background-color: #222;
  color: #FFF; }
  @media (max-width: 576px) {
    .linkfloater {
      display: none; } }
  .linkfloater a:link, .linkfloater a:visited, .linkfloater a:hover {
    color: #00FF00;
    text-decoration: underline; }

